@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

// Variables

$primary: #2f3186;
$secondary: #ff4a55;
$black: #000;
$green: #02bc7d;
$white: #fff;
$red: #f24646;
$light-gray: #c9c9c9;
$yellow: #ffb352;
$black: #000;
$light-purple: rgba(57, 49, 134, 0.94);
$widescreen: 1199px;
$desktop: 1023px;
$tablet: 767px;
$phone: 479px;

*,
*::after,
*::before {
	box-sizing: border-box;
}

* {
	padding: 0;
	margin: 0;
	outline: none;
}

body {
	font: 300 16px/1.875 Montserrat, arial, sans-serif;
	font-size: 16px;
	line-height: 1.875;
	color: #000;
	min-width: 320px;

	@media (max-width: $desktop) {
		font-size: 15px;
	}

	@media (max-width: 767px) {
		font-size: 14px;
	}
}

.container {
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 15px;
}

form,
fieldset {
	margin: 0;
	padding: 0;
	border-style: none;
}

input[type="text"],
input[type="tel"],
input[type="email"],
input[type="search"],
input[type="password"],
textarea,
.form-control {
	-webkit-appearance: none;
	-webkit-border-radius: 0;
	box-sizing: border-box;
	padding: 20px 27px;
	font-size: 14px;
	line-height: 23px;
	font-weight: 500;
	color: #261f25;
	border-radius: 5px;
	border: 1px solid rgba(0, 0, 0, 0.07);
	font-family: Montserrat, arial, sans-serif;

	@media (max-width: $desktop) {
		padding: 15px 20px;
	}

	&:focus {
		outline: none;
		border-color: rgba(0, 0, 0, 0.2);
	}

	&::placeholder {
		color: #999;
		opacity: 1;
	}
}

select {
	-webkit-border-radius: 0;
}

textarea {
	resize: vertical;
	vertical-align: top;
	resize: none;
	height: 170px;
}

button,
input[type="button"],
input[type="reset"],
input[type="file"],
input[type="submit"] {
	-webkit-appearance: none;
	-webkit-border-radius: 0;
	cursor: pointer;
	font-family: Montserrat, arial, sans-serif;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

a {
	text-decoration: none;
	color: #000;
}


ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

h2 {
	font-size: 36px;
	text-transform: uppercase;
	text-align: center;

	@media (max-width: 1023px) {
		font-size: 30px;
	}

	@media (max-width: 767px) {
		font-size: 24px;
	}

	br {

		@media (max-width: 767px) {
			display: none;
		}
	}
}

span {
	position: relative;
}

h1 span::after,
h2 span::after {
	content: "";
	background-image: url("../images/yellow-bg.png");
	position: absolute;
	width: 100%;
	height: 20px;
	background-repeat: no-repeat;
	z-index: -1;
	bottom: -9px;
	left: 15%;

	@media (max-width: 1023px) {
		height: 15px;
		background-size: 100% 100%;
		left: 10%;
	}
}

h3 {
	font-size: 20px;
	text-transform: uppercase;

	@media (max-width: 1023px) {
		font-size: 18px;
	}

}

h4 {
	font-size: 20px;
	text-transform: uppercase;

	@media (max-width: 1023px) {
		font-size: 18px;
	}
}

h5 {
	font-size: 14px;
	text-transform: capitalize;
}

a,
button {
	transition: all 0.3s ease-in-out;
}

.btn {
	border: 1px solid transparent;
	border-radius: 5px;
	position: relative;
	color: #fff;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	font-size: 16px;
	line-height: 22px;
	padding: 23px 20px;
	text-transform: uppercase;
	font-weight: 700;
	box-shadow: 0 3px 9px rgba(116, 118, 206, 0.5);

	@media (max-width: 1023px) {
		font-size: 13px;
		padding: 15px 20px;
	}

	&:hover {
		text-decoration: none;
	}

	&.btn-primary {
		background: #2f3186;

		&:hover {
			background: #12145c;
		}
	}

	&.btn-secondary {
		background: $secondary;

		&:hover {
			background: #ff2b38;
		}
	}

	&.btn-white {
		background-color: #fff;
		color: #000;
		border: 2px solid #eaeaea;
		box-shadow: none;
		padding: 9px 18px;

		&:hover {
			border-color: #2f3186;
			background-color: #2f3186;
			color: #fff;
		}
	}

	&.btn-sm {
		padding: 13px 26px;
		font-weight: 500;
		text-transform: capitalize;
	}

	&.btn-lg {
		font-size: 18px;

		@media (max-width: 1023px) {
			font-size: 16px;
		}
	}
}

.btn-block {

	> .btn {
		margin: 20px 15px 0;
		max-width: calc(100% + 30px);
	}
}

.view-more-btn {
	font-size: 14px;
	text-transform: capitalize;

	i {
		margin-left: 18px;

		@media (max-width: 1023px) {
			margin-left: 12px;
		}
	}
}


.header {
	position: relative;
	z-index: 5;
	background-color: #eceef3;
	padding-top: 70px;
	font-size: 16px;
	line-height: 1.5;
	font-weight: 500;

	@media (max-width: 1199px) {
		font-size: 14px;
	}

	@media (max-width: 1023px) {
		padding-top: 0;
	}

	.header-wrapper {
		padding: 20px 0;
		border-top: 1px solid #dfe2e9;

		@media (max-width: 1023px) {
			border: 0;
		}
	}

	.container {
		max-width: 1550px;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}

	.header-left {
		display: flex;
		align-items: center;
	}

	.header-right {
		flex-grow: 1;
		flex-basis: 0;
		min-width: 0;
	}

	.logo {
		margin: 0 30px 0 0;

		@media (max-width: 1399px) {
			margin: 0 20px 0 0;
		}

		@media (max-width: 359px) {
			margin: 0 12px 0 0;
		}
	}
}

.logo {
	width: 141px;
	display: block;

	@media (max-width: 1199px) {
		width: 120px;
	}

	@media (max-width: 359px) {
		width: 100px;
	}

	a {
		display: block;
	}

	img {
		display: block;
		width: 100%;
		height: auto;

		&.logo-blue {
			display: block;
		}

		&.logo-white {
			display: none;
		}
	}
}

.nav-opener {
	width: 40px;
	height: 40px;
	display: flex !important;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	position: absolute;
	right: 15px;
	top: 50%;
	transform: translateY(-50%);
	transition-property: right !important;
	background: #2f3186;
	border-radius: 5px;

	@media (min-width: 1024px) {
		display: none !important;
	}

	.nav-active & {

		@media (max-width: 1023px) {
			right: 300px;
		}

		@media (max-width: 359px) {
			right: 274px;
		}
	}

	&::before,
	&::after,
	span {
		width: 24px;
		height: 2px;
		background: #fff;
		content: "";
		display: block;
		transition: all 0.3s ease-in-out;
	}

	&::before {
		margin-bottom: 6px;

		.nav-active & {
			margin-bottom: -2px;
			transform: rotate(45deg);
		}
	}

	&::after {
		margin-top: 6px;

		.nav-active & {
			margin-top: -2px;
			transform: rotate(-45deg);
		}
	}

	span {

		.nav-active & {
			opacity: 0;
		}
	}
}

.nav-active {

	@media (max-width: 1023px) {
		overflow: hidden;
	}
}

.header-holder {
	position: relative;

	@media (max-width: 1023px) {
		position: fixed;
		top: 0;
		bottom: 0;
		right: -280px;
		overflow: auto;
		width: 280px;
		background: #2f3186;
		transition: right 0.3s ease-in-out;

		@media (max-width: 359px) {
			width: 265px;
		}
	}

	.nav-active & {

		@media (max-width: 1023px) {
			right: 0;
		}
	}

	.header-wrap {

		@media (max-width: 1023px) {
			padding: 30px 25px;
			width: 100%;
			height: 100%;
			display: flex;
			flex-flow: row wrap;
		}
	}
}

.header-info {
	position: absolute;
	right: 0;
	top: -64px;
	display: flex;
	align-items: center;

	@media (max-width: 1023px) {
		position: static;
		flex-flow: row wrap;
		justify-content: flex-start;
		flex-direction: column-reverse;
		margin-top: auto;
		width: 100%;
		order: 2;
	}
}

.social-networks {
	display: flex;
	padding: 0;
	margin: 0 -9px;
	list-style-type: none;
	justify-content: end;
	font-size: 20px;
	line-height: 1;

	@media (max-width: 1023px) {
		width: calc(100% + 18px);
		justify-content: space-between;
	}

	li {
		padding: 0 9px;

		a {
			text-decoration: none;
			color: #c9ccd3;
			display: block;

			@media (max-width: 1023px) {
				color: #fff;
			}

			&:hover {
				color: #2f3186;

				@media (max-width: 1023px) {
					color: #fff;
					opacity: 0.7;
				}
			}
		}
	}
}

.header-contect {
	color: #000;
	margin: 0 0 0 20px;

	@media (max-width: 1023px) {
		color: #fff;
		margin: 0 0 20px;
		width: 100%;
	}

	a {
		color: #000;

		@media (max-width: 1023px) {
			color: #fff;
		}

		&:hover {
			color: #2f3186;

			@media (max-width: 1023px) {
				color: #fff;
				opacity: 0.5;
			}
		}
	}
}

.main-nav {
	list-style-type: none;
	display: flex;
	align-items: center;
	justify-content: flex-end;

	@media (max-width: 1023px) {
		display: block;
		width: 100%;
		order: 1;
	}

	li {
		margin: 0 18px;

		@media (max-width: 1399px) {
			margin: 0 12px;
		}

		@media (max-width: 1199px) {
			margin: 0 7px;
		}

		@media (max-width: 1023px) {
			margin: 0;
			padding: 10px 0;
		}

		&:last-child {
			margin-right: 0;
		}

		a:not(.btn) {
			text-decoration: none;
			color: #000;
			text-transform: capitalize;

			@media (max-width: 1023px) {
				color: #fff;
				display: block;
			}

			&:hover {
				color: #2f3186;

				@media (max-width: 1023px) {
					color: #fff;
					opacity: 0.7;
				}
			}
		}

		.btn {
			margin-left: 25px;

			@media (max-width: 1399px) {
				margin-left: 10px;
			}

			@media (max-width: 1199px) {
				font-size: 14px;
				padding: 10px 20px;
			}

			@media (max-width: 1023px) {
				margin-left: 0;
				margin-top: 10px;
				width: 100%;
				border: 1px solid #fff;
			}
		}

		&.active {

			a {
				color: #2f3186;

				@media (max-width: 1023px) {
					color: #fff;
					opacity: 0.5;
				}
			}
		}
	}
}

.courses-menu {
	position: relative;
	padding: 0 0 0 24px;
	border-left: 1px solid #dfe2e9;

	@media (max-width: 1399px) {
		padding: 0 0 0 20px;
	}

	@media (max-width: 767px) {
		position: static;
	}

	@media (max-width: 359px) {
		position: static;
		padding: 0 0 0 12px;
	}

	li {
		position: relative;
		padding: 10px 0;

		@media (max-width: 767px) {
			position: static;
		}

		a {
			color: #2f3186;
			display: flex;
			align-items: center;

			i {
				padding: 0 12px 0 0;
				font-size: 11px;

				&.fa-th {
					font-size: 15px;
				}

				&.fa-chevron-down {
					margin-left: 10px;
					padding: 0;
				}
			}

			&:hover {
				color: #7b7db2;
			}
		}

		&.courses-drop {

			&.courses-drop-active {

				.courses-dropdown {
					opacity: 1;
					visibility: visible;
					transform: scaleY(1);
				}
			}
		}
	}

	.courses-dropdown {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		background-color: white;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
		width: 260px;
		padding: 0 20px;
		top: 45px;
		left: 0;
		transform: scaleY(0);
		transform-origin: left top;
		transition: all 0.3s ease-in-out;
		font-size: 14px;
		line-height: 20px;
		border-radius: 10px;
		padding: 10px 13px;

		@media (max-width: 767px) {
			width: 100%;
			border-radius: 0;
			top: 80px;
			max-height: 70vh;
			overflow: auto;
		}

		li {
			padding: 14px 25px;

			@media (max-width: 767px) {
				padding: 14px 20px;
			}

			+ li {
				border-top: 1px solid #f3f3f3;
			}

			a {
				text-transform: uppercase;
				color: #898e9a;
				display: flex;
				align-items: center;
				justify-content: space-between;

				i {
					padding: 0;

					@media (max-width: 767px) {
						transform: rotate(90deg);
					}
				}

				&:hover {
					color: #2f3186;
				}
			}

			&.courses-submenu {

				&.courses-submenu-active {

					.courses-submenu-drop {
						opacity: 1;
						visibility: visible;
						transform: scaleX(1);

						@media (max-width: 767px) {
							display: block;
						}
					}
				}
			}
		}
	}

	.courses-submenu-drop {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		background-color: white;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
		width: 260px;
		padding: 0 20px;
		top: -10px;
		left: 100%;
		margin-left: 20px !important;
		transform: scaleX(0);
		transform-origin: left top;
		transition: all 0.3s ease-in-out;
		font-size: 14px;
		line-height: 20px;
		border-radius: 10px;
		padding: 10px 13px;

		@media (max-width: 767px) {
			width: 100%;
			margin: 15px 0 0 !important;
			position: static;
			transform: scaleY(0);
			display: none;
			box-shadow: none;
			border: 0;
			padding: 0;
		}

		li {

			@media (max-width: 767px) {
				padding: 12px 15px;
			}
		}
	}
}

.visual-banner {
	background: #eceef3;
	position: relative;

	@media (max-width: 767px) {
		padding: 30px 0 0;
	}

	&::after {
		position: absolute;
		left: 0;
		bottom: 26.5%;
		content: "";
		background: url("../images/hero-1-semicircle.png") no-repeat;
		background-size: 100% 100%;
		width: 86px;
		height: 281px;

		@media (max-width: 1399px) {
			display: none;
		}
	}

	.container {
		width: 100%;
		max-width: 1210px;
	}

	.banner-frame {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0;
		position: relative;
		width: 100%;

		@media (max-width: 767px) {
			display: block;
		}
	}

	.column-left {
		padding: 5px 0 0;
		position: relative;
		width: 55%;

		@media (max-width: 1399px) {
			width: 48%;
		}

		@media (max-width: 767px) {
			width: 100%;
			margin: 0 0 30px;
			text-align: center;
		}

		&::before {
			content: "";
			position: absolute;
			background: url("../images/hero-1-circle.png") no-repeat;
			background-size: 100% 100%;
			width: 56px;
			height: 59px;
			top: -21.6%;
			left: -28%;

			@media (max-width: 1199px) {
				display: none;
			}
		}
	}

	h1 {
		font-size: 72px;
		line-height: 1.2;
		margin: 0 0 20px;
		letter-spacing: -3px;

		@media (max-width: 1399px) {
			font-size: 60px;
		}

		@media (max-width: 1199px) {
			font-size: 50px;
		}

		@media (max-width: 1023px) {
			font-size: 38px;
			margin: 0 0 15px;
			letter-spacing: -1px;
		}

		@media (max-width: 767px) {
			font-size: 30px;
		}

		br {

			@media (max-width: 767px) {
				display: none;
			}
		}

		span {
			color: #2f3186;
			text-transform: capitalize;
			font-weight: 500;
			padding: 0 0 8px;
			display: inline-block;

			@media (max-width: 767px) {
				padding: 0;
			}

			&::after {
				left: 3%;
				bottom: 20%;

				@media (max-width: 767px) {
					bottom: -10%;
				}
			}
		}
	}

	.courses-box {
		font-size: 24px;
		font-weight: 500;
		color: #000;
		letter-spacing: -1px;

		@media (max-width: 1199px) {
			font-size: 20px;
		}

		@media (max-width: 1023px) {
			font-size: 18px;
		}

		@media (max-width: 767px) {
			font-size: 15px;
		}
	}

	.btn-block {
		margin-top: 90px;

		@media (max-width: 1399px) {
			margin-top: 50px;
		}

		@media (max-width: 1023px) {
			margin-top: 25px;
		}

		> .btn {
			margin: 0;
			min-width: 200px;

			@media (max-width: 1023px) {
				min-width: 150px;
			}
		}
	}

	.column-right {
		width: 41%;
		position: relative;
		padding-top: 10px;

		@media (max-width: 1399px) {
			width: 46%;
		}

		@media (max-width: 767px) {
			width: 100%;
		}

		&::before {
			content: "";
			position: absolute;
			background: url("../images/dots.png") no-repeat;
			background-size: 100% 100%;
			width: 46px;
			height: 73px;
			top: 19.1%;
			right: -32.2%;

			@media (max-width: 1199px) {
				display: none;
			}
		}
	}

	.img-holder {
		margin: 0 -24% 0 0;

		@media (max-width: 1599px) {
			margin: 0 -15% 0 0;
		}

		@media (max-width: 1399px) {
			margin: 0;
			padding-right: 5%;
		}

		@media (max-width: 767px) {
			margin: 0 auto;
			max-width: 360px;
			padding-right: 0;
		}

		img {
			width: 100%;
			height: auto;
			display: block;
		}
	}
}

.visual-slider {
	position: relative;
	z-index: 1;

	.slick-dots {
		position: absolute;
		list-style: none;
		margin: 0;
		padding: 0;
		top: 50%;
		right: 3.5%;
		transform: translateY(-50%);

		@media (max-width: 1599px) {
			right: 20px;
		}

		@media (max-width: 1199px) {
			right: 15px;
		}

		@media (max-width: 767px) {
			right: 10px;
		}

		li {
			padding: 5px 0;

			button {
				font-size: 16px;
				line-height: 1;
				font-weight: 700;
				width: 50px;
				height: 50px;
				padding: 0;
				cursor: pointer;
				position: relative;
				color: #d8dbe1;
				border-radius: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				background: transparent;
				border: 1px solid #d8dbe1;

				/* border: 1px solid rgba(255, 255, 255, 0.1); */
