			[class*=" col-block"] {
				width: 100% !important;
			}
		}
	}

	[class^="col-block"],
	[class*=" col-block"] {
		padding: 30px 15px 0;
		width: 100%;
	}

	>.col-block {

		@media (min-width: ($tablet + 1)) {

			&-1 {
				width: calc(100/12 * 1%);
			}

			&-2 {
				width: calc(100/12 * 2%);
			}

			&-3 {
				width: calc(100/12 * 3%);
			}

			&-4 {
				width: calc(100/12 * 4%);
			}

			&-5 {
				width: calc(100/12 * 5%);
			}

			&-6 {
				width: calc(100/12 * 6%);
			}

			&-7 {
				width: calc(100/12 * 7%);
			}

			&-8 {
				width: calc(100/12 * 8%);
			}

			&-9 {
				width: calc(100/12 * 9%);
			}

			&-10 {
				width: calc(100/12 * 10%);
			}

			&-11 {
				width: calc(100/12 * 11%);
			}

			&-12 {
				width: calc(100/12 * 12%);
			}
		}
	}
}

