	}

	&.widget-checklist {
		background: #f2f4f8;
		color: #261f25;
		border-radius: 8px;
		padding: 30px 27px;
		font-size: 14px;

		@media (max-width: $widescreen) {
			padding: 20px;
		}

		ul {
			margin: 0;
			padding: 0;
			list-style: none;

			li {
				margin: 0 0 24px;

				@media (max-width: $widescreen) {
					margin: 0 0 15px;
				}

				&:last-child {
					margin-bottom: 0;
				}

				.custom-checkbox {
					position: relative;

					input {
						display: none;

						&:checked + .label {

							.checkbox {
								border-color: $primary;
								background: $primary;

								i {
									opacity: 1;
								}
							}
						}
					}

					.label {
						display: block;
						position: relative;
						font-size: 14px;
						color: #261f25;
						text-transform: uppercase;
						line-height: 1.2;
						padding: 2px 0 0 30px;

						.checkbox {
							position: absolute;
							width: 18px;
							height: 18px;
							border-radius: 3px;
							left: 0;
							top: 1px;
							border: 2px solid #cdcfd5;
							background: $white;
							display: flex;
							text-align: center;
							align-items: center;
							justify-content: center;
							transition: all 0.3s ease-in-out;

							i {
								color: $white;
								font-size: 10px;
								line-height: 1;
								opacity: 0;
								transition: all 0.3s ease-in-out;
							}
						}
					}
				}
			}
		}
	}

	.blogs & {
		margin: 0 0 65px;

		@media (max-width: $widescreen) {
