					display: block;
					border-radius: 5px;
					width: 100%;
				}
			}

			h5 {

				@media (max-width: $desktop) {
					font-size: 12px;
				}

				@media (max-width: $tablet) {
					font-size: 15px;
				}

				a {

					&:hover {
						color: $primary;
					}
				}
			}
