	}

	&.widget-blog-cat {
		color: #53545b;
		font-size: 14px;

		ul {
			margin: 0;
			padding: 0;
			list-style: none;

			li {
				position: relative;
				padding: 0 0 30px;

				@media (max-width: $widescreen) {
					padding: 0 0 20px;
				}

				&:last-child {
					padding: 0;
				}

				a {
					display: block;
					position: relative;
					padding: 0 0 0 20px;
					font-weight: 700;
					color: inherit;
					text-transform: uppercase;

					&::before {
						position: absolute;
						left: 0;
						top: 10px;
						border-radius: 50%;
						width: 7px;
						height: 7px;
						transition: all 0.3s ease-in-out;
						background: #b2b1bd;
						content: "";
					}

					&:hover {
						color: $primary;

						&::before {
							background: $primary;
						}
					}
				}
			}
		}
	}
}

.table-small {
