				margin-top: -10px;
			}
		}

		.tick-list {
			line-height: 2.3;
		}

		.image-holder {

			@media (min-width: 1440px) {
				width: 97%;
				margin: 55px 0 0 64px;
			}
		}
	}

	.col-block-6 {

		&.scholarship-achivers {
			margin: 0 auto;

			@media (min-width: 1440px) {
				display: flex;
				justify-content: end;
			}

			@media (max-width: $tablet) {
				max-width: 504px;
			}
		}

		.blog-post {
			border-radius: 10px;
			overflow: hidden;
			box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
			padding: 0 !important;

			@media (min-width: 1440px) {
				width: 88%;
			}

			@media (max-width: 1439px) {
				width: 100%;
			}
		}
	}

	.image-holder {

		&.play-circle {
			position: relative;

			.overlay {
				position: absolute;
				height: 100%;
				top: 0;
				background-color: $black;
				opacity: 0.3;
				width: 100%;
			}

			.btn-play {
				border-radius: 50%;
				height: 90px;
				width: 90px;
				border: 1px solid $red;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: $red;
				color: $white;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);

				@media (max-width: $desktop) {
					width: 65px;
					height: 65px;
				}

				@media (max-width: $tablet) {
					width: 55px;
					height: 55px;
				}

				i {
					font-size: 40px;

					@media (max-width: $desktop) {
						font-size: 30px;
					}

					@media (max-width: $tablet) {
						font-size: 24px;
					}
				}
			}
		}
	}

	.text-box {
		background: $white;
		padding: 27px 18px;
		display: flex;

		.image-icon {
			width: 50px;
			height: 50px;
			padding: 5px;
			margin-right: 15px;
			background: #2f3186;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			border: 3px solid $yellow;
			flex-shrink: 0;
		}

		.image-desc {
			color: $light-gray;
			flex-grow: 1;
			overflow: hidden;

			h3 {
				color: $black;
				margin: 0 0 5px;
				font-weight: 700;
				text-transform: capitalize;
			}
		}
	}

	&.interbase-scholarship {
		background-color: $primary;
		color: $white;
		position: relative;

		@media (min-width: 1440px) {
			padding: 86px 0 100px;
		}

		&::after {
			content: "";
			position: absolute;
			background: url("../images/interbase-section-shape.png") no-repeat;
			background-size: 100% 100%;
			height: 320px;
			width: 37.656%;
			right: 0;
			bottom: 0;
			right: 0;

			@media (min-width: 1440px) {
				width: 37.856%;
				height: 342px;
			}

			@media (max-width: 1439px) {
				height: 250px;
			}

			@media (max-width: $desktop) {
				height: 120px;
			}

			@media (max-width: $tablet) {
				height: 100px;
			}
		}

		h2 {
			color: $white;
		}

		h3 {
			color: $white;
			text-transform: uppercase;
		}

		h4 {
			font-size: 16px;
			color: $white;
			text-transform: capitalize;
		}

		.image-holder {

			img {
				width: 100%;
			}
		}
	}
}

.page-inner {

	.tick-list {

		&.yellow {

			li {

				&::before {
					background: url(../images/ico-tick-yellow.png) no-repeat;
				}
			}
		}
	}
}
